<template>
    <div class="selection-component">
      <div class="selection-show" @click="toggleDrop">
        <span>{{selections[selectedIndex].label}}</span>
        <div class="arrow"></div>
      </div>
      <div class="selection-list" v-show="isDrop">
        <ul>
        	<li v-for="(item, index) in selections" 
        		@click="chooseSelection(index)">{{item.label}}</li>
        </ul>
      </div>
    </div>
</template>

<script>
import {eventHub} from '../../eventHub'

export default {
	props: {
		selections: {
			type: Array,
			default: () => {
				return [{
					label: 'test',
					value: 0
				},{
					label: 'test1',
					value: 1
				}]
			}
		}
	},
	data () {
		return {
			selectedIndex: 0,
			isDrop: false
		}
	},
	methods: {
		toggleDrop (event) {
			let oldStatus = this.isDrop
			event.stopPropagation()
			eventHub.$emit('close-up')
			this.isDrop = !oldStatus
		},
		chooseSelection (newIndex) {
			this.selectedIndex = newIndex
			this.isDrop = false
			this.$emit('on-change',this.selections[this.selectedIndex])
		}
	},
	mounted () {
		eventHub.$on('close-up', () => {
			this.isDrop = false
		})
	}
}
</script>

<style scoped>
.selection-component{
	position: relative;
	display: inline-block;
}
.selection-show{
	border: 1px solid #e3e3e3;
	padding: 0 20px 0 10px;
	display: inline-block;
	position: relative;
	cursor: pointer;
	height: 25px;
	line-height: 25px;
	border-radius: 3px;
	background-color: #fff;
}
.selection-show .arrow {
	display: inline-block;
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
	border-top: 5px solid #e3e3e3;
	width: 0px;
	height: 0px;
	margin-top: -1px;
	margin-left: 6px;
	margin-right: -14px;
	vertical-align: middle;
}
.selection-list {
	display: inline-block;
	position: absolute;
	left: 0;
	top: 25px;
	width: 100%;
	background-color: #fff;
	border-top: 1px solid #e3e3e3;
	border-bottom: 1px solid #e3e3e3;
	z-index: 5;
}
.selection-list li{
	padding: 5px 15px 5px 10px;
	border-left: 1px solid #e3e3e3;
	border-right: 1px solid #e3e3e3;
	cursor: pointer;
	background-color: #fff;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
.selection-list li:hover{
	background: #e3e3e3;
}
</style>


